body {
  font-family: 'Ubuntu', sans-serif;
  font-size: 14px;
}

.container-all {
  display: flex;
  align-items: stretch;

  max-width: 1024px;
  margin-right: auto;
  margin-left: auto;
}

.container {
  padding: 20px;

  &__left {
    background-color: rgba(5, 4, 65, 0.5);
    width: 35%;
  }

  // .container__bottom-right

  &__right {
    width: 65%;
    background-color: rgba(0, 0, 0, 0.212);
  }
}

.image-thumb {
  border-radius: 10px;
  max-width: 240px;
  max-height: 240px;
  margin-right: auto;
  margin-left: auto;

  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.cv-image {
  display: block;
  width: 100%;
  object-fit: cover;
}
